html, body {
    height: 100%;
    width: 100%;
    overflow-x: hidden
}

html {
    margin: 0;
    line-height: 170%;
    -ms-overflow-style: -ms-autohiding-scrollbar
}

body {
    color: #5f5f5f;
    font-family: noto sans, segoe ui, helvetica neue, Helvetica, Roboto, FreeSans, sans-serif;
    margin: 0;
    font-size: 1.1rem
}

article p a {
    text-decoration: underline;
    text-decoration-skip-ink: auto;
    color: #5f5f5f
}

article .post-footer > a {
    text-decoration: none
}

h1 {
    font-size: 1.9em;
    padding-top: .2em;
    margin: .5em 0 .75em;
    font-family: noto serif, segoe ui, helvetica neue, Helvetica, Roboto, FreeSans, serif;
    font-weight: 600;
    line-height: 1.3em
}

h2, h3, h4, h5, h6 {
    margin: 1.25em 0 .75em;
    font-family: noto serif, segoe ui, helvetica neue, Helvetica, Roboto, FreeSans, serif;
    font-weight: 600
}

h2 {
    font-size: 1.25em;
    line-height: 1.4em
}

h2 a {
    text-decoration: none;
    position: relative;
    text-shadow: 0 -2px 0 #fff, 0 -1px 0 #fff, 0 0 0 #fff, 2px -2px 0 #fff, 2px -1px 0 #fff, 2px 0 0 #fff, -2px -2px 0 #fff, -2px -1px 0 #fff, -2px 0 0 #fff, 1px -2px 0 #fff, 1px -1px 0 #fff, 1px 0 0 #fff, -1px -2px 0 #fff, -1px -1px 0 #fff, -1px 0 0 #fff, 0 -2px 0 #fff, 0 -1px 0 #fff, 0 0 0 #fff;
    box-shadow: 0 -1px 0 0 #fff inset, 0 -2px 0 0 transparent inset;
    background-size: 1px 1em;
    position: relative;
    transition: all .6s cubic-bezier(0.075, 0.82, 0.165, 1)
}

h2 a:hover {
    box-shadow: 0 -1px 0 0 #fff inset, 0 -2px 0 0 #5f5f5f inset;
    transition: all 1s cubic-bezier(0.075, 0.82, 0.165, 1)
}

h5, h6 {
    font-weight: 700;
    line-height: 1.3em
}

h4 {
    line-height: 1.3em
}

h3 {
    font-size: 1.1em;
    line-height: 1.3em
}

p {
    margin: 0 0 1em
}

a {
    text-decoration: underline;
    text-decoration-skip-ink: auto;
    color: #5f5f5f
}

a, a > svg {
    transition: .1s cubic-bezier(0.075, 0.82, 0.165, 1)
}

a:visited {
    color: #5f5f5f
}

.color-link {
    text-decoration: none;
    color: #63bda2;
    font-family: noto sans, segoe ui, helvetica neue, Helvetica, Roboto, FreeSans, sans-serif;
    margin-right: 1.5em
}

.color-link:visited {
    color: #63bda2
}

.color-link:hover {
    color: #359377
}

.color-link:hover .color-arrow {
    fill: #359377
}

.tag {
    margin-right: .75em;
    margin-bottom: 1em;
    line-height: 1.75em;
    color: #ababab
}

.tag:visited {
    color: #ababab
}

.tag:hover {
    color: #898989
}

time {
    margin-right: 1.5em;
    color: #ababab
}

hr {
    border: 0;
    height: 0;
    border-bottom: 1px solid #d2d2d2
}

ul, ol {
    margin: 1em 0;
    padding-left: 2em
}

li {
    line-height: 150%
}

mark {
    background-color: #ffec5c
}

blockquote {
    border-left: 4px solid #d2d2d2;
    padding-left: 1.3em;
    margin-left: 0;
    margin-top: 0
}

blockquote p {
    margin-bottom: 0
}

cite {
    font-size: .95em
}

pre {
    padding: 1em;
    background-color: #f7f7f7;
    max-width: 100%;
    overflow: scroll
}

p code, kbd {
    font-size: .95rem;
    background: #f7f7f7;
    padding: .2em
}

code, pre, kbd {
    font-family: Menlo, Monaco, courier new, monospace;
    font-size: .95rem;
    line-height: 154%
}

.table-wrapper {
    overflow-x: auto
}

table {
    max-width: 100%;
    border-spacing: 0
}

table thead {
    background: #f7f7f7
}

table th, table td {
    padding: .5em 1em;
    border: 1px double #eee
}

nav {
    padding: .25em 1.5em 1em;
    max-width: 100%
}

.site-title {
    margin: 1.5em 0 1em;
    text-align: center;
    line-height: 1.7em
}

.site-title a {
    font-family: noto serif, segoe ui, helvetica neue, Helvetica, Roboto, FreeSans, serif;
    font-size: 1.5em;
    text-decoration: none;
    margin-top: .75em
}

.nav-menu {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap
}

.nav-link {
    margin-bottom: .5em
}

.description {
    font-style: italic;
    color: #898989;
    text-align: center;
    margin-bottom: 3.7em;
    margin-top: -.75em
}

.content-container {
    padding: .5em 1.5em 0;
    margin: 0;
    max-width: 100%
}

.paginator {
    flex-shrink: 0;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-top: 1em;
    padding-bottom: 1.5em;
    align-items: center
}

.paginator .older {
    margin-left: 1em
}

.paginator p {
    font-size: .95rem;
    margin: 0
}

.paginator a {
    text-decoration: none;
    color: #63bda2;
    font-size: .95rem
}

.article-content {
    flex: 1 0 auto
}

.article-figure {
    margin: 0 0 1em
}

.article-image {
    max-width: 100%;
    margin: 0 0 1em;
    /* height: 400px; */
}

.canon {
    width: 100%;
    height: auto
}

.footnote-ref a {
    margin-left: .3em
}

.footnote-ref a::before {
    content: "["
}

.footnote-ref a::after {
    content: "]"
}

.emojify {
    font-family: Apple Color Emoji, Segoe UI Emoji, NotoColorEmoji, Segoe UI Symbol, Android Emoji, EmojiSymbols;
    font-size: 1.5rem;
    vertical-align: middle
}

.twitter-tweet {
    font-family: noto sans, segoe ui, helvetica neue, Helvetica, Roboto, FreeSans, sans-serif !important;
    border-left: 4px solid #63bda2 !important;
    color: #5f5f5f !important;
    font-size: 1rem !important;
    font-style: italic !important;
    margin-left: 0;
    padding-left: 1.3em !important
}

.twitter-tweet p {
    font-size: 1.1em;
    font-style: normal;
    margin-bottom: 1em;
    line-height: 155%
}

.twitter-tweet a {
    color: #5f5f5f !important;
    text-decoration: underline !important
}

.twitter-tweet a:hover, .twitter-tweet a:focus {
    text-decoration: underline !important
}

.__h_instagram.card {
    font-family: noto sans, segoe ui, helvetica neue, Helvetica, Roboto, FreeSans, sans-serif !important;
    font-size: 1.1em !important;
    border: 1px solid #ababab;
    margin-top: 1.2em !important
}

.kofi-button {
    margin: .75em 0 .5em
}

.post-footer {
    font-size: .95rem;
    color: #ababab;
    border-bottom: 1px solid #d2d2d2;
    padding-bottom: 1.8em
}

.hidden {
    visibility: hidden;
    border-bottom: 0 solid
}

.page-footer {
    padding: 1.5em 0 2em;
    font-size: .95rem;
    color: #ababab
}

.page-footer a {
    text-decoration: none;
    color: #ababab
}

.footer-divider {
    color: #d2d2d2;
    margin-bottom: 1.5em
}

.archives-list {
    padding-left: 0
}

.archives-list-item {
    display: flex;
    margin: .5em 0
}

.archives-list-item-date {
    width: 60px;
    margin-right: .25em;
    color: #ababab
}

.not-found-title {
    margin-bottom: 0
}

.footer {
    visibility: hidden;
    height: 0
}

.footer-mobile {
    margin-top: 2em;
    max-width: 100%;
    padding: 0 1.5em .75em;
    overflow: hidden
}

.footer-mobile .footer-mobile-links {
    display: flex;
    justify-content: center
}

.footer-mobile .divider-bar {
    color: #ababab;
    padding: 0 .25em
}

footer {
    text-align: center
}

footer .social-icons {
    margin: 0 0 1.25em -.5em
}

footer .social-icon {
    padding: .3em .5em;
    margin: .32em .1em;
    display: inline-block;
    height: 2em;
    position: relative
}

footer .social-icon > svg:hover {
    fill: #898989
}

footer a {
    text-decoration: none;
    font-size: .95rem;
    color: #ababab
}

footer a:visited {
    color: #ababab
}

footer a:hover {
    color: #898989
}

@media only screen and (min-width: 900px) {
    nav {
        position: fixed;
        overflow: auto;
        height: 100%;
        top: 0;
        left: 36px;
        width: 240px;
        padding: 0
    }

    .site-title {
        margin: 3em 0 1em;
        text-align: left
    }

    .nav-menu {
        display: flex;
        flex-direction: column;
        align-items: flex-start
    }

    .nav-link {
        margin-bottom: .5em
    }

    .description {
        text-align: left;
        padding: 1.55em 0 .5em;
        border-bottom: none;
        margin-bottom: 1em;
        margin-top: 0
    }

    .article-content {
        margin-top: 1.4em
    }

    .paginator {
        padding-bottom: 2em
    }

    .content-container {
        max-width: 680px;
        margin-left: 310px;
        padding: 0 1.5em 0 0;
        height: 100%;
        display: flex;
        flex-direction: column
    }

    .content-container h1 {
        font-size: 1.9em;
        border-top: none;
        padding-top: 0;
        margin-top: 1.4em
    }

    .post-title {
        border-top: none;
        padding-top: 0;
        margin-top: .75em
    }

    .page-footer {
        border-bottom: none
    }

    .footer {
        position: relative;
        visibility: visible;
        margin-top: 1em;
        text-align: left
    }

    .footer-mobile {
        visibility: hidden;
        height: 0;
        padding: 0;
        margin: 0;
        overflow: hidden
    }

    .social-icons {
        width: 100%
    }
}

figcaption {
    text-align: center;
    font-style: italic
}


